import test from 'ava';
const server = require('../server.js');

const WebSocket = require('ws');  

test.cb('connection', t => {      
    var ws = new WebSocket('ws://localhost:8080');
    ws.on('open', function open() {
        t.pass();
    });  
    ws.on('error', function handleError(error) {
        t.fail(error);
    });      
    ws.on('close', function handleError(reason) {
        t.end();
    });       
})

test.cb('Setting Name', t => {      
    ws.on('message', function incoming(message) {
        if(message.substring(0,2) == "01"){
            t.is(message.substring(0,24), '01{"name":"testName123",');
            t.end();
        }
    }); 
    ws.send('01{"name":"testName123"}');                 
})

test.cb('Sending message', t => {      
    ws.on('message', function incoming(message) {
            console.log(message);
    });
    ws.send('02{"message":"testMessage123"}');                 
})